import React from "react";
import AnimatedCursor from "react-animated-cursor"

function Pointer():JSX.Element {
  return (
    <div className="App">
    <AnimatedCursor
      innerSize={8}
      outerSize={8}
      color='0, 255, 255'
      outerAlpha={0.6}
      innerScale={0.7}
      outerScale={6}
      clickables={[
        'a',
        'input[type="text"]',
        'input[type="email"]',
        'input[type="number"]',
        'input[type="submit"]',
        'input[type="image"]',
        'label[for]',
        'select',
        'textarea',
        'button',
        '.link'
      ]}
    />
    </div>
  );
}

export default Pointer