import { configureStore, createSlice } from '@reduxjs/toolkit';

const userSlice = createSlice({
  name: 'user',
  initialState: { name: '현아', age: 98 },
  reducers: {
    changeName: (state) => {
      state.name = 'park';
    },
    increase: (state, action) => {
      state.age += action.payload;
    },
  },
});

export const { changeName, increase } = userSlice.actions;

const cartSlice = createSlice({
  name: 'cart',
  initialState: [
    { id: 0, imgurl: 'shoes1.png', name: 'White and Black', count: 2 },
    { id: 1, imgurl: 'shoes2.png', name: 'Red Knit', count: 1 },
    { id: 2, imgurl: 'shoes3.png', name: 'Grey Yordan', count: 1 },
  ],
  reducers: {
    addCount: (state, action) => {
      const index = state.findIndex((item) => item.id === action.payload);
      if (index !== -1) {
        state[index].count++;
      }
    },
    decreaseCount: (state, action) => {
      const index = state.findIndex((item) => item.id === action.payload);
      if (index !== -1) {
        if (state[index].count > 0) {
          state[index].count--;
        } else if (state[index].count === 0) {
          alert('상품이 더 이상 없습니다.');
        }
      }
    },
    addItem: (state, action) => {
      const index = state.findIndex((item) => item.id === action.payload.id);
      if (index !== -1) {
        state[index].count++;
      } else {
        state.push(action.payload);
      }
    },
    deleteItem: (state, action) => {
      const index = state.findIndex((item) => item.id === action.payload);
      if (index !== -1) {
        state.splice(index, 1);
      }
    },
    sortName: (state) => {
      state.sort((a, b) => (a.name > b.name ? 1 : -1));
    },
  },
});

export const {
  addCount,
  decreaseCount,
  addItem,
  deleteItem,
  sortName,
} = cartSlice.actions;

export default configureStore({
  reducer: {
    user: userSlice.reducer,
    cart: cartSlice.reducer,
  },
});